import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'lista_modulos-dados',   templateUrl: 'lista_modulos_dados.component.html' }) export class Lista_modulosDadosComponent implements OnInit {       displayMode: Lista_modulosDadosDisplayModeEnum;     displayModeEnum = Lista_modulosDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = Lista_modulosDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = Lista_modulosDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = Lista_modulosDadosDisplayModeEnum.Edit;           break;       }     }  }  enum Lista_modulosDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


